.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 30;
  background-color: rgba(17, 17, 17, .6);
  justify-content: center;
  align-items: center;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.modal {
  background-color: #fff;
  padding: 28px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.backdrop.is-hidden .modal {
  transform: scale(1.1);
}

.close-btn {
  width: 18px;
  height: 18px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

@media screen and (min-width: 1440px) {
  .close-btn {
    width: 24px;
    height: 24px;
    top: 18px;
    right: 18px;
  }
}

.close-btn__icon {
  width: 14px;
